// Angular
import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
// RxJS
import { finalize, takeUntil, tap } from 'rxjs/operators';
// Translate
import { TranslateService } from '@ngx-translate/core';
// NGRX
import { Store } from '@ngrx/store';
import { AppState } from '../../../../core/reducers';
// Auth
import { AuthNoticeService, AuthService, Register, User } from '../../../../core/auth/';
import { Subject } from 'rxjs';
import { ConfirmPasswordValidator } from './confirm-password.validator';
import { RegisterService } from './register.service';
import { JwtService } from '../../../../jwt/jwt.service';

@Component({
	selector: 'kt-register',
	templateUrl: './register.component.html',
	encapsulation: ViewEncapsulation.None
})
export class RegisterComponent implements OnInit, OnDestroy {
	userForm: FormGroup;
	loading = false;
	errors: any = [];
	actionType: string;
	registerType: string;

	private unsubscribe: Subject<any>; // Read more: => https://brianflove.com/2016/12/11/anguar-2-unsubscribe-observables/

	/**
	 * Component constructor
	 *
	 * @param authNoticeService: AuthNoticeService
	 * @param translate: TranslateService
	 * @param router: Router
	 * @param auth: AuthService
	 * @param store: Store<AppState>
	 * @param fb: FormBuilder
	 * @param cdr
	 */
	constructor(
		private authNoticeService: AuthNoticeService,
		private translate: TranslateService,
		private router: Router,
		private auth: AuthService,
		private store: Store<AppState>,
		private fb: FormBuilder,
		private cdr: ChangeDetectorRef,
		private registerService: RegisterService,
		private jwt: JwtService,
		private activeRoute: ActivatedRoute
	) {
		this.unsubscribe = new Subject();
	}

	/*
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
    */

	/**
	 * On init
	 */
	ngOnInit() {
		this.userForm = this.fb.group({
			mobile: ['', Validators.required]
		});
		// this.getIp();
		this.actionType = 'Register';
		this.activeRoute.queryParams.subscribe(param => {
			this.registerType = (param && param.type === 'forgot') ? 'Forgot Password' : 'Register';
		})
	}

	/*
    * On destroy
    */
	ngOnDestroy(): void {
		this.unsubscribe.next();
		this.unsubscribe.complete();
		this.loading = false;
	}

	/**
	 * Checking control validation
	 *
	 * @param controlName: string => Equals to formControlName
	 * @param validationType: string => Equals to valitors name
	 */
	isControlHasError(controlName: string, validationType: string): boolean {
		const control = this.userForm.controls[controlName];
		if (!control) {
			return false;
		}

		const result = control.hasError(validationType) && (control.dirty || control.touched);
		return result;
	}

	register() {
		const data = this.registerType === 'Forgot Password' ? { ...this.userForm.value, isForgot: true } : this.userForm.value;
		this.registerService.registerUser(this.actionType, { params: data }).subscribe(response => {
			this.checkVerificationStatus(response);
		}, error => {
			console.log(error);
		});
	}

	// getIp() {
	//   this.ipService.returnIp().subscribe(response => {
	//     this.dummy['ip'] = response['ip'];
	//   }, error => {
	//     console.log(error);
	//   });
	// }

	checkVerificationStatus(response) {
		this.userForm.addControl('otp', new FormControl('', Validators.required));
		this.actionType = 'Verify OTP';
		this.cdr.detectChanges();
		if (response.result.token) {
			this.jwt.setToken(response.result.token);
			this.router.navigateByUrl('auth/choose-password');
		}
	}
}
